import React, {useEffect} from 'react';

import addJavascript from '../functions/addJavascript';


function Navbar() {

    useEffect(() => {

         addJavascript('js/navbar.js');

    },[])


    return (
        //  Desktop Navbar
        <>
        <nav className="app-navbar">
        <div className="app-navbar__logo" >
              DAVIK <sup>TM</sup>
          </div>

            <ul>
                
                <li className="nav-item"><a href="#offer">What I Offer</a></li>
                <li className="nav-item"><a href="#about">Skills</a></li>
                <li className="nav-item"><a href="#projects">Projects</a></li>
                <li className="nav-item "><a className="contact-button" target="_blank" href="https://wa.me/+2348164497136">Contact</a></li>
                
                
            </ul>
        </nav>

 {/* Mobile Navbar */}

     <nav className="app-navbar-mobile nav-mobile-item"> 

          <div className="app-navbar-mobile__logo" >
              DAVIK <sup>TM</sup>
          </div>

          <a target="_blank" href="https://wa.me/+2348164497136" className="contact-button " style={{textDecoration: 'none', fontSize: '1.5rem'}}>
             CONTACT
          </a>

          <div className="app-navbar-mobile__hamburger nav-mobile-item">
                <div className="hamburger hamburger--1"></div>
                <div className="hamburger hamburger--2"></div>
                <div className="hamburger hamburger--3"></div>
          </div>



     </nav>

     <div className="navbar-menu">

       <div className="navbar-mobile-items">
                <a data-id="#offer"  className="navbar-mobile-link">WHAT I OFFER</a>
                <a  data-id="#about"  className="navbar-mobile-link">ABOUT</a>
                <a data-id="#projects"  className="navbar-mobile-link">PROJECTS</a>
                {/* <a data-id="#contact" className="navbar-mobile-link">CONTACT</a> */}
                <div className="navbar-menu__socials">
                <a target="_blank" href="https://github.com/davik20"><i className="fa fa-github"/></a>
                           <a target="_blank" href="https://twitter.com/iamdavik"> <i className="fa fa-twitter"/></a>
                           <a target="_blank" href="https://wa.me/+2348164497136" > <i className="fa fa-whatsapp"/></a>
                           <a target="_blank" href="mailto:onyejivic@gmail.com"> <i className="fa fa-envelope"/></a>
                </div>
       </div>
          
     </div>
     {/* Mobile Navbar */}
     </>

    
    )
}

export default Navbar
