import React from 'react'

function About() {
    return (
        <section id="about" className="about margin-top-large">
    
        <div className="heading-component"> 
            <h1 className="heading-component__h1 ">About</h1>
            <p className="heading-component__p">I'm well versed in a lot of technologies and i am very passionate about learning new technologies</p>
        </div>

        <div className="about__content">
            <div>

                <p className="paragraph1 text-center"> I build things, beautiful things! I'm very detail oriented and can create rich web applications with complex user interfaces. </p>

                <p className="paragraph1 text-center">I have over 2 years of experience with full stack development. I'm competent in frameworks such as laravel and NodeJs, Front-end libraries such as React and Redux.   </p>
                <p className="paragraph1 text-center">Here are some technologies i'm familiar with</p>
               
                <div className="about__skills">
                     <ul>
                         <li className="about__icon"> <i className=" fas fa-arrow-right"></i> HTML/CSS</li>
                         <li className="about__icon"> <i className="fas fa-arrow-right"></i> JavaScript</li>
                         <li className="about__icon"> <i className="fas fa-arrow-right"></i> Bootstrap</li>
                         <li className="about__icon"> <i className="fas fa-arrow-right"></i> SASS/SCSS</li>
                         <li className="about__icon"> <i className="fas fa-arrow-right"></i> Material UI</li>

                         <li className="about__icon"> <i className="fas fa-arrow-right"></i> React</li>
                         <li className="about__icon"> <i className="fas fa-arrow-right"></i> Redux</li>
                         
                     </ul>
                     <ul>
                         <li className="about__icon"> <i className="fas fa-arrow-right"></i> PHP</li>
                         <li className="about__icon"> <i className="fas fa-arrow-right"></i> Node</li>
                         <li className="about__icon"> <i className="fas fa-arrow-right"></i> MongoDb</li>
                         <li className="about__icon"> <i className="fas fa-arrow-right"></i> MYSQL</li>
                         <li className="about__icon"> <i className="fas fa-arrow-right"></i> Laravel</li>
                         <li className="about__icon"> <i className="fas fa-arrow-right"></i> Wordpress</li>
                         <li className="about__icon"> <i className="fas fa-arrow-right"></i> Git</li>
                     </ul>
                </div>
            </div>

                <div className="about__image">
                       <img src={`${process.env.PUBLIC_URL}/img/victor.jpg`} alt="" />
                </div>
        </div>
        
    </section>
    )
}

export default About
