import React from 'react'

function project({project, renderType }) {
    const {type, title, description, technologies, img, links} = project

    if(renderType === 1) {

        return (
            <div className="project2">
           
    
            <div className="project__details2">
                <h5 className="project__type2 ">{type}</h5>
                <h5 className="project__title2 opacity-reduced">{title}</h5>
                <div className="project__description2">
    
                    <p className="paragraph1" style={{opacity: 1}}>{description}</p>
                </div>
    
                <div className="project__technologies2 opacity-reduced">
                  {technologies.map(technology => <p> {technology} </p>)}
                </div>
    
                <div className="project__links2">
                { links.github &&  <a target="_blank" href={`https://${links.github}`}> <i className="fa fa-github"> </i></a> }
                <a target="_blank" href={`https://${links.link}`}> <i className="fa fa-link"> </i></a>
                </div>
    
            </div>
    
            <div className="project__img2">
            <img  src={img}/>
       </div>
    
    
        </div>
        )
    }

    else if(renderType===2) {

        return (
         <div class="project">
        <div class="project__img">
             <img  src={img}/>
        </div>

        <div class="project__details">
            <h5 class="project__type ">{type}</h5>
            <h5 class="project__title opacity-reduced">{title}</h5>
            <div class="project__description">

                <p class="paragraph1" style={{opacity: 1}}>{description}</p>
            </div>

            <div class="project__technologies opacity-reduced">
            {technologies.map(technology => <p> {technology} </p>)}
            </div>

            <div class="project__links">
                { links.github &&  <a target="_blank" href={`https://${links.github}`}> <i className="fa fa-github"> </i></a> }
                <a target="_blank" href={`https://${links.link}`}> <i className="fa fa-link"> </i></a>
            </div>

        </div>


    </div>
        )
    }
}

export default project
