import React, {useState} from 'react'



function Socials() {
    let [mode, setMode] = useState('night')


    const changeMode= () => {

     setMode(mode === 'night' ? 'day': 'night')  

     if(mode === 'night'){
         makeDay()
     }else {
         makeNight()
     }
    }

    function makeDay () {
         /* --blue: rgb(151, 151, 202);
    --lightBlue: rgb(105,77, 240);
    --lightPurple: rgb(191,10,117);
    --darkBlue: rgb(15,12,31) ;
    --background: white;
    --white: rgb(15,12,31);
    --whiteFaded: rgb(15,12,31); */


            document.documentElement.style.setProperty('--white', 'rgb(15,12,31)' )
            document.documentElement.style.setProperty('--blue', ' rgb(151, 151, 202)' )
            document.documentElement.style.setProperty('--lightBlue', 'rgb(105,77, 240)' )
            document.documentElement.style.setProperty('--darkBlue', 'rgb(15,12,31)' )
            document.documentElement.style.setProperty('--background', 'white' )
           
            document.documentElement.style.setProperty('--whiteFaded', 'rgb(15,12,31)' )


     }

     function makeNight(){
        document.documentElement.style.setProperty('--blue', 'rgb(151, 151, 202)')
        document.documentElement.style.setProperty('--lightBlue', 'rgb(105,77, 240)')
        document.documentElement.style.setProperty('--lightPurple', 'rgb(191,10,117)')
        document.documentElement.style.setProperty('--darkBlue', 'rgb(15,12,31)')
        document.documentElement.style.setProperty('--background', 'rgb(15,12,31)')
        document.documentElement.style.setProperty('--white', 'white' )
        document.documentElement.style.setProperty('--whiteFaded', 'rgb(255, 255, 255, .8)')

     }


    return (

          <div className="socials "> 

              <div className="socials__links">
                           
                            <a target="_blank" href="https://github.com/davik20"><i className="fa fa-github"/></a>
                           <a target="_blank" href="https://twitter.com/iamdavik"> <i className="fa fa-twitter"/></a>
                           <a target="_blank" href="https://wa.me/+2348164497136"> <i className="fa fa-whatsapp"/></a>

                            <p className="socials__line"></p>
              </div>

              <div onClick={changeMode} className="socials__displayMode">
                  { mode === 'night' ? <i className="fa fa-sun"  style={{color: 'yellow', cursor: 'pointer'}}></i> : <i className="far fa-moon"  style={{color: 'rgb(15,12,31)', cursor: 'pointer'}}></i> }
              </div>

             
          </div>
    )
}

export default Socials
