import React, {useState} from 'react';
import Project from './project';



function Projects() {

    const [projects, setProjects] = useState( [
        {
            img: `${process.env.PUBLIC_URL}/img/board_meeting.jpg`,
            type: 'Featured Project',
            title: ' Landing Page',
            description: 'A fully animated landing page that utilizes technologies like React and React-transition-group ',
            technologies: [
                'react',
                'react-transition-group',
               
                'vsCode'
            ],
            links: {
                github: 'github.com/davik20/landing_page',
                link: 'davik20.github.io/landing_page'
            }
        },
        {
            img: `${process.env.PUBLIC_URL}/img/board_meeting.jpg`,
            type: 'Featured Project',
            title: ' Ecommerce Project',
            description: 'A React E-commerce project, implements functionalities like search, filtering, sorting, pagination. Connected to a NodeJs and express backend with MongoDb as the database ',
            technologies: [
                'react',
                'node',
                'redux',
                'Mongodb',
                'vsCode'
            ],
            links: {
                github: false,
                link: 'davikshopping.herokuapp.com'
            }
        },
        // {
        //     img: `${process.env.PUBLIC_URL}/img/board_meeting.jpg`,
        //     type: 'Featured Project',
        //     title: ' A Davik Project',
        //     description: 'I used Something else to do this as a form of expereiemet',
        //     technologies: [
        //         'react',
        //         'node',
        //         'wordpress',
        //         'php',
        //         'vsCode'
        //     ],
        //     links: {
        //         github: 'github.com',
        //         link: 'google.com'
        //     }
        // }
    ]
    )


    let renderType = 1

    return (
        <section id="projects" class="projects margin-top-large">
        <div class="heading-component"> 
            <h1 class="heading-component__h1">Projects</h1>
            <p class="heading-component__p">I'm well versed in a lot of technologies and i am passionate about learning new technologies</p>
        </div>
        
        <div class="projects_box">

            {projects.map(project => {

                renderType = renderType === 1 ? 2 : 1
            
           return <Project project={project} renderType={renderType} />
             
            
     })}

        
        </div>

    </section>
    )
}

export default Projects
